@font-face {
  font-family: 'Lora';
  src: local('Lora'), url(./fonts/Lora-VariableFont_wght.ttf) format('truetype');
}
@font-face {
  font-family: 'Lora Italic';
  src: local('Lora Italic'), url(./fonts/Lora-Italic-VariableFont_wght.ttf) format('truetype');
}

.App {
  position: relative;
  background-color: #282c34;
  background: radial-gradient(#444, #000);
  min-height: 100vh;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #ffffff;
}

.App .quote-container {
  font-family: 'Lora', 'Times New Roman', Times, serif;
  padding: 5rem;
}

.quote-container .quote-body {
  font-family: 'Lora Italic';
  font-size: 4rem;
}

.quote-container .quote-author {
  margin-top: 2rem;
  font-size: 1.5rem;
}

.quote-container .quote-author::before {
  content: '\2014\00A0';
}

@media(max-width: 720px) {
  .App .quote-container {
    padding: 2rem;
  }

  .quote-container .quote-body {
    font-size: 2.5rem;
  }
}

.button-refresh {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  background: none;
  border: none;
}

.spin {
  -webkit-animation:spin 0.5s linear;
  -moz-animation:spin 0.5s linear;
  animation:spin 0.5s linear;
}


@-moz-keyframes spin { 100% { -moz-transform: rotate(360deg); } }
@-webkit-keyframes spin { 100% { -webkit-transform: rotate(360deg); } }
@keyframes spin { 100% { -webkit-transform: rotate(360deg); transform:rotate(360deg); } }